<template></template>
<script>
  export default{
    computed:{
      menuActiveName: {
      	get () { return this.$store.state.common.menuActiveName },
      	set (val) { this.$store.commit('common/updateMenuActiveName', val) }
      },
      mainTabsActiveName: {
        get () { return this.$store.state.common.mainTabsActiveName },
        set (val) { this.$store.commit('common/updateMainTabsActiveName', val) }
      },
      systemId: {
        get() {return this.$store.state.common.systemId;}
      }
    },
    created () {
      this.menuList = JSON.parse(sessionStorage.getItem('menuList') || '[]')
      this.dynamicMenuRoutes = JSON.parse(sessionStorage.getItem('dynamicMenuRoutes') || '[]')
      this.getFirstMenu(this.menuList)
    },
    methods:{
      getFirstMenu(memuList){
        console.log('-------------')
        console.log(memuList)
        for(var i=0;i<memuList.length;i++){
          var menu=memuList[i];
          if(menu.sysId == this.systemId){
            var obj=menu.children && menu.children.length>=1?menu.children[0]:menu;
            this.menuActiveName=obj.id;
            this.mainTabsActiveName=obj.name
            this.gotoRouteHandle(obj)
            break;
          }
        }
      },
      gotoRouteHandle (menu) {
        console.log(menu)
        var route = this.dynamicMenuRoutes.filter(item => item.meta.menuId === menu.id)
        console.log(route)
        if (route.length >= 1) {
          this.$router.push({ name: route[0].name })
        }
      }
    },
    watch:{
      systemId(){
        this.getFirstMenu(this.menuList)
      }
    }
  }
</script>



